import React, { useState, useEffect } from "react";
import { CircleToBlockLoading } from "react-loadingg";
import { Carousel as AntdCarousel } from "antd";
export function Summary({ formData }) {
  const { url_data } = formData;

  const [summaries, setSummaries] = useState(null);
  const [urls, setUrls] = useState(null);
  const [topics, setTopics] = useState(null);
  const [loading, setloading] = useState(false);
  const data = async (url_data, url_server) => {
    const datas = await fetch(
      "https://summarizer-txt.herokuapp.com/get_summary",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          selected_url: url_data,
          server_data: url_server,
        }),
      }
    )
      .then((response) => response.json())
      .then((json) => {
        setSummaries(json.data);
        setTopics(json.topic);
        setUrls(json.url);
      });
    setloading(true);
  };
  useEffect(() => {
    data(formData.url_data, formData.url_server_data);
  }, []);
  return (
    <React.Fragment>
      {loading ? (
        <div>
          {summaries.map(function (summary_data, i) {
            return (
              <div key={i} className="card">
                <div className="card-body">
                  <div className="card-title">{topics[i]}</div>
                  <p className="card-text">{summary_data}</p>
                </div>
              </div>
            );
          })}
        </div>
      ) : (
        <CircleToBlockLoading size="large" color="#00ffff" />
      )}
      ;
    </React.Fragment>
  );
}
