import React from "react";
import { Input as AntdInput } from "antd";

export const TopicForm = ({ formData, setForm, navigation }) => {
  const { topic_name } = formData;

  return (
    <div>
      <form>
        <label>Enter Topic name</label>
        <AntdInput
          type="text"
          name="topic_name"
          onChange={setForm}
          value={topic_name}
        />
        <input type="submit" value="Next" onClick={() => navigation.next()} />
      </form>
    </div>
  );
};
