import React, { useState, useEffect } from "react";
import { Select, Spin, Space } from "antd";
import { CircleToBlockLoading } from "react-loadingg";
import "antd/dist/antd.css";
const { Option } = Select;
export const UrlForm = ({ formData, setForm, navigation }) => {
  var { url_data } = formData;

  const [urls, setUrls] = useState(null);
  const [loading, setloading] = useState(false);
  const data = async (data) => {
    const datas = await fetch("https://summarizer-txt.herokuapp.com/get_url", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((json) => {
        setUrls(json.description);
        formData.url_server_data = [json.urls, json.description];
      });
    setloading(true);
  };

  useEffect(() => {
    data(formData.topic_name);
  }, []);
  function onChangeHandler(e) {
    //url_selected = event.target.value;
    let value = Array.isArray(e) ? e.map((x) => x) : [];
    formData.url_data = value;
    //console.log(url_data);
  }
  return (
    <React.Fragment>
      {loading ? (
        <form>
          <label>Select Articles to generate summary</label>
          <Select
            mode="multiple"
            allowClear
            style={{ width: "100%" }}
            placeholder="Please select"
            name="url_data"
            onChange={onChangeHandler}
          >
            if({urls})
            {urls.map(function (url_data, i) {
              return (
                <Option key={i} value={url_data}>
                  {url_data}
                </Option>
              );
            })}
          </Select>
          <input
            type="submit"
            value="Summarize"
            onClick={() => navigation.next()}
          />
        </form>
      ) : (
        <CircleToBlockLoading size="large" color="#00ffff" /> //<Spin tip="Loading..." />
      )}
    </React.Fragment>
  );
};
